          style: 'solid',
          color: '#000000'
        },
        borderLeft: {
          width: 0,
          style: 'solid',
          color: '#000000'
        },
        borderRight: {
          width: 0,
          style: 'solid',
          color: '#000000'
        },
        borderBottom: {
          width: 0,
          style: 'solid',
