    return {
      value: {
        header: '<img src="https://res.cloudinary.com/dxjptlkpn/image/upload/w_1000,c_limit/v1648325610/ereqjwyqwciiowijk3do.png" uploading="false" width="100" style="max-width:100%" id="">',
        body: '<p>Hello <span data-tag="" class="ptag" data-id="first_name" data-label="first_name">{{first_name}}</span></p><p>Hope you are having an amazing day so far. Here is a demo of the upcoming email editor for Engage (<a target="_blank" rel="noopener noreferrer nofollow" href="https://engage.so/">engage.so</a>).</p><p>Feel free to play around with the settings to the right -&gt;&gt;</p><div data-type="button" href="https://engage.so/" style="display:inline-block;padding:10px 20px;text-decoration:none">Oh, and there is a button</div><p>And a divider:</p><hr style="height:1px;border:0;background-color:#ddd;"><p>:)</p>',
