      arr.push('background-color:' + this.body.bgColor)
      arr.push('padding:' + this.body.padding + 'px')
      arr.push('border-radius:' + this.body.borderRadius + 'px')
      arr.push('color:' + this.body.fontColor)
      arr.push('font-family:' + this.body.fontFamily + ',sans-serif')
      arr.push('font-size:' + this.body.size + 'px')
      if (this.body.borderToggleSide === 'all') {
        arr.push('border:' + this.body.borderAll.width + 'px ' +
          this.body.borderAll.style + ' ' +
          this.body.borderAll.color)
      } else {
        arr.push('border-left:' + this.body.borderLeft.width + 'px ' +
          this.body.borderLeft.style + ' ' +
          this.body.borderLeft.color)
        arr.push('border-right:' + this.body.borderRight.width + 'px ' +
          this.body.borderRight.style + ' ' +
          this.body.borderRight.color)
        arr.push('border-top:' + this.body.borderTop.width + 'px ' +
          this.body.borderTop.style + ' ' +
          this.body.borderTop.color)
        arr.push('border-bottom:' + this.body.borderBottom.width + 'px ' +
          this.body.borderBottom.style + ' ' +
          this.body.borderBottom.color)
      }
      if (this.body.marginToggleSide === 'all') {
        arr.push('margin:' + this.body.marginAll + 'px ')
      } else {
        arr.push('margin-top:' + this.body.marginTop + 'px ')
        arr.push('margin-right:' + this.body.marginRight + 'px ')
        arr.push('margin-bottom:' + this.body.marginBottom + 'px ')
        arr.push('margin-left:' + this.body.marginLeft + 'px ')
      }
      return arr.join(';')
    },
    footerStyle () {
      const arr = []
      arr.push('background-color:' + this.footer.bgColor)
      arr.push('padding:' + this.footer.padding + 'px')
      arr.push('border-radius:' + this.footer.borderRadius + 'px')
      arr.push('color:' + this.footer.fontColor)
      arr.push('font-family:' + this.footer.fontFamily + ',sans-serif')
      arr.push('font-size:' + this.footer.size + 'px')
      if (this.footer.borderToggleSide === 'all') {
        arr.push('border:' + this.footer.borderAll.width + 'px ' +
          this.footer.borderAll.style + ' ' +
          this.footer.borderAll.color)
      } else {
        arr.push('border-left:' + this.footer.borderLeft.width + 'px ' +
          this.footer.borderLeft.style + ' ' +
          this.footer.borderLeft.color)
        arr.push('border-right:' + this.footer.borderRight.width + 'px ' +
          this.footer.borderRight.style + ' ' +
          this.footer.borderRight.color)
        arr.push('border-top:' + this.footer.borderTop.width + 'px ' +
          this.footer.borderTop.style + ' ' +
          this.footer.borderTop.color)
        arr.push('border-bottom:' + this.footer.borderBottom.width + 'px ' +
          this.footer.borderBottom.style + ' ' +
          this.footer.borderBottom.color)
      }
      return arr.join(';')
    }
  },
  methods: {
    expandSection (section, toggle) {
      if (section === 'general') {
        this.expandGeneral = toggle ? !this.expandGeneral : true
      } else {
        this.expandGeneral = false
      }
      if (section === 'header') {
        this.expandHeader = toggle ? !this.expandHeader : true
      } else {
        this.expandHeader = false
      }
      if (section === 'body') {
        this.expandBody = toggle ? !this.expandBody : true
      } else {
        this.expandBody = false
      }
      if (section === 'footer') {
        this.expandFooter = toggle ? !this.expandFooter : true
      } else {
        this.expandFooter = false
      }
      if (section === 'button') {
        this.expandButton = toggle ? !this.expandButton : true
      } else {
        this.expandButton = false
      }
    },
    _diff (obj1, obj2) {
      const diff = Object.keys(obj1).reduce((diff, key) => {
        if (!obj2[key]) {
          return diff
        }
        if (obj1[key] === obj2[key]) {
          return diff
        }
        if (typeof obj1[key] === 'object' && typeof obj2[key] === 'object') {
          const inner = this._diff(obj1[key], obj2[key])
          if (!Object.keys(inner).length) {
            return diff
          }
          return {
            ...diff,
            [key]: inner
          }
        }

        return {
          ...diff,
          [key]: obj2[key]
        }
      }, {})

      return diff
    },
    getSettings () {
      return {
        general: this._diff(this.defaultTemplate.general, this.template.general),
        header: this._diff(this.defaultTemplate.header, this.template.header),
        body: this._diff(this.defaultTemplate.body, this.template.body),
        footer: this._diff(this.defaultTemplate.footer, this.template.footer),
        button: this._diff(this.defaultTemplate.button, this.template.button)
      }
    },
    getHTML () {
    },
    getContent () {
      return this.value
    }
  }
}
</script>

