<template>
  <div v-if="editor" class="tiptap-editor" :style="{'--btn-br':  btnradius + 'px', '--btn-color':  btncolor, '--btn-bg':  btnbg}">
    <floating-menu :editor="editor" class="floating-menu">
      <button @click="editor.chain().focus().toggleHeading({ level: 1 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 1 }) }">
        <format-header1 />
      </button>
      <button @click="editor.chain().focus().toggleHeading({ level: 2 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 2 }) }">
        <format-header2 />
      </button>
      <button @click="editor.chain().focus().toggleHeading({ level: 3 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 3 }) }">
        <format-header3 />
      </button>
      <button @click="editor.chain().focus().toggleBulletList().run()" :class="{ 'is-active': editor.isActive('bulletList') }">
        <format-list-bulleted />
      </button>
      <button @click="editor.chain().focus().toggleOrderedList().run()" :class="{ 'is-active': editor.isActive('orderedList') }">
        <format-list-numbered />
      </button>
      <button>
        <label><image-area /><span style="display:none"><input type="file" accept="image/*" @change="uploadImage"></span></label>
      </button>
      <button @click="editor.chain().focus().setHorizontalRule().run()">
        <minus />
      </button>
      <button @click="editor.chain().focus().addButton().run()">
        <gesture-tap-button />
      </button>
    </floating-menu>

    <bubble-menu :editor="editor" v-show="!editor.isActive('customimage') && !editor.isActive('button')" class="bubble-menu">
      <button @click="editor.chain().focus().toggleHeading({ level: 1 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 1 }) }">
        <format-header1 />
      </button>
      <button @click="editor.chain().focus().toggleHeading({ level: 2 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 2 }) }">
        <format-header2 />
      </button>
      <button @click="editor.chain().focus().toggleHeading({ level: 3 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 3 }) }">
        <format-header3 />
      </button>
      <button @click="editor.chain().focus().toggleBold().run()" :class="{ 'is-active': editor.isActive('bold') }">
        <format-bold />
      </button>
      <button @click="editor.chain().focus().toggleItalic().run()" :class="{ 'is-active': editor.isActive('italic') }">
        <format-italic />
      </button>
      <button @click="openLink" :class="{ 'is-active': editor.isActive('link') }">
        <link-variant />
      </button>
      <button @click="editor.chain().focus().toggleUnderline().run()" :class="{ 'is-active': editor.isActive('underline') }">
        <format-underline />
      </button>
      <button @click="editor.chain().focus().toggleStrike().run()" :class="{ 'is-active': editor.isActive('strike') }">
        <format-strikethrough />
      </button>
      <button @click="editor.chain().focus().setTextAlign('left').run()" :class="{ 'is-active': editor.isActive({ textAlign: 'left' }) }">
        <format-align-left />
      </button>
      <button @click="editor.chain().focus().setTextAlign('center').run()" :class="{ 'is-active': editor.isActive({ textAlign: 'center' }) }">
        <format-align-center />
      </button>
      <button @click="editor.chain().focus().setTextAlign('right').run()" :class="{ 'is-active': editor.isActive({ textAlign: 'right' }) }">
        <format-align-right />
      </button>
      <div class="link" v-show="showLinkPop">
        <form @submit.prevent="onSubmitLink"><input type="text" ref="url" @blur="hideLinkPop" placeholder="https://" v-model="linkUrl"><button type="button" @click="unsetLink"><close /></button></form>
      </div>
    </bubble-menu>

    <bubble-menu :editor="editor" v-show="editor.isActive('customimage') && !editor.getAttributes('customimage').uploading" :tippyOptions="{ placement: 'top-start' }" class="bubble-menu">
      <div>
        <div style="padding:3px">Width: <input type="number" min="36" style="width:60px" v-model="imgWidth"></div>
        <div style="padding:3px">
          <form @submit.prevent="editor.chain().focus().updateAttributes('customimage', { url: imageLinkUrl }).run()">Link: <input type="url" placeholder="https://" style="width200px" v-model="imageLinkUrl"><!--button type="submit">Set</button--><button type="button" @click="unsetImageURL" style="position:relative;top:4px"><close-circle-outline /></button></form>
          </div>
      </div>
    </bubble-menu>

    <bubble-menu :editor="editor" v-show="editor.isActive('button')" :tippyOptions="{ placement: 'top-start' }" class="bubble-menu">
      <div style="padding:3px">
        <form @submit.prevent="editor.commands.updateAttributes('button', { href: btnLinkUrl })">Link: <input type="url" placeholder="https://" style="width200px" v-model="btnLinkUrl"><!--button type="submit">Set</button--><button type="button" @click="unsetBtnURL" style="position:relative;top:4px"><close-circle-outline /></button></form>
      </div>
    </bubble-menu>

    <editor-content :editor="editor" :value="value" />
  </div>
</template>

<script>
import tippy from 'tippy.js'
import { Editor, EditorContent, VueRenderer, FloatingMenu, BubbleMenu } from '@tiptap/vue-2'
import TextAlign from '@tiptap/extension-text-align'
import Underline from '@tiptap/extension-underline'
import Mention from '@tiptap/extension-mention'
import MentionList from './MentionList.vue'
import StarterKit from '@tiptap/starter-kit'
import Placeholder from '@tiptap/extension-placeholder'
import Link from '@tiptap/extension-link'
import TextStyle from '@tiptap/extension-text-style'

import Button from './extensions/button'
import Tag from './extensions/tag'
import CustomImage from './extensions/custom-image'
import uploadFile from './extensions/upload'

import FormatBold from 'vue-material-design-icons/FormatBold.vue'
import FormatStrikethrough from 'vue-material-design-icons/FormatStrikethrough.vue'
import FormatUnderline from 'vue-material-design-icons/FormatUnderline.vue'
import FormatItalic from 'vue-material-design-icons/FormatItalic.vue'
import FormatHeader1 from 'vue-material-design-icons/FormatHeader1.vue'
import FormatHeader2 from 'vue-material-design-icons/FormatHeader2.vue'
import FormatHeader3 from 'vue-material-design-icons/FormatHeader3.vue'
import FormatListBulleted from 'vue-material-design-icons/FormatListBulleted.vue'
import FormatListNumbered from 'vue-material-design-icons/FormatListNumbered.vue'
import LinkVariant from 'vue-material-design-icons/LinkVariant.vue'
import Close from 'vue-material-design-icons/Close.vue'
import CloseCircleOutline from 'vue-material-design-icons/CloseCircleOutline.vue'
import ImageArea from 'vue-material-design-icons/ImageArea.vue'
import Minus from 'vue-material-design-icons/Minus.vue'
// import Plus from 'vue-material-design-icons/Plus.vue'
import FormatAlignLeft from 'vue-material-design-icons/FormatAlignLeft.vue'
import FormatAlignCenter from 'vue-material-design-icons/FormatAlignCenter.vue'
import FormatAlignRight from 'vue-material-design-icons/FormatAlignRight.vue'
import GestureTapButton from 'vue-material-design-icons/GestureTapButton.vue'

export default {
  name: 'TiptapEditor',
  components: {
    FormatBold,
    FormatStrikethrough,
    FormatUnderline,
    FormatItalic,
    FormatHeader1,
    FormatHeader2,
    FormatHeader3,
    FormatListBulleted,
    FormatListNumbered,
    LinkVariant,
    Close,
    CloseCircleOutline,
    ImageArea,
    // Plus,
    Minus,
    GestureTapButton,
    FormatAlignLeft,
    FormatAlignCenter,
    FormatAlignRight,

    EditorContent,
    FloatingMenu,
    BubbleMenu
  },

  props: {
    value: {
      type: String
    },
    tags: {
      type: Array,
      default () {
        return []
      }
    },
    mentions: {
      type: Array,
      default () {
        return []
      }
    },
    photos: {
      type: Array
    },
    placeholder: {
      type: String
    },
    bg: {
      type: String
    },
    btnbg: {
      type: String,
      default: '#1e87db'
    },
    btncolor: {
      type: String,
      default: '#ffffff'
    },
    btnradius: {
      type: Number,
      default: 3
    }
  },

  watch: {
    imgWidth (n, o) {
      this.editor.chain().focus().updateAttributes('customimage', { width: n }).run()
    }
  },

  data () {
    return {
      imageTab: this.photos?.length ? 'gallery' : 'upload',
      showLinkPop: false,
      linkUrl: '',
      imgWidth: 0,
      btnLinkUrl: '',
      imageLinkUrl: '',
      editor: null,
      autofocus: false
    }
  },

  mounted () {
    const extensions = [
      StarterKit.configure({
        horizontalRule: {
          HTMLAttributes: {
            style: 'height:1px;border:0;background-color:#ddd;'
          }
        }
      }),
      Placeholder.configure({
        placeholder: this.placeholder ?? 'Write something...'
      }),
      Tag.configure({
        HTMLAttributes: {
          class: 'ptag'
        },
        suggestion: {
          char: '{{',
          items: ({ query }) => {
            return this.tags.filter(item => item.toLowerCase().startsWith(query.toLowerCase())).slice(0, 10)
          },
          render: () => {
            let component
            let popup

            return {
              onStart: props => {
                component = new VueRenderer(MentionList, {
                  parent: this,
                  propsData: props
                })

                popup = tippy('body', {
                  getReferenceClientRect: props.clientRect,
                  appendTo: () => document.body,
                  content: component.element,
                  showOnCreate: true,
                  interactive: true,
                  trigger: 'manual',
                  placement: 'bottom-start'
                })
              },
              onUpdate (props) {
                component.updateProps(props)

                popup[0].setProps({
                  getReferenceClientRect: props.clientRect
                })
              },
              onKeyDown (props) {
                if (props.event.key === 'Escape') {
                  popup[0].hide()

                  return true
                }

                return component.ref?.onKeyDown(props)
              },
              onExit () {
                popup[0].destroy()
                component.destroy()
              }
            }
          }
        }
      }),
      Mention.configure({
        HTMLAttributes: {
          class: 'mention'
        },
        renderLabel ({ options, node }) {
          return `${options.suggestion.char}${node.attrs.label ?? node.attrs.id}`
        },
        suggestion: {
          char: '@',
          items: ({ query }) => {
            return this.mentions.filter(item => {
              return item.value
                ? item.value.toLowerCase().startsWith(query.toLowerCase())
                : item.toLowerCase().startsWith(query.toLowerCase())
            }).slice(0, 10)
          },
          render: () => {
            let component
            let popup

            return {
              onStart: props => {
                component = new VueRenderer(MentionList, {
                  parent: this,
                  propsData: props
                })

                popup = tippy('body', {
                  getReferenceClientRect: props.clientRect,
                  appendTo: () => document.body,
                  content: component.element,
                  showOnCreate: true,
                  interactive: true,
                  trigger: 'manual',
                  placement: 'bottom-start'
                })
              },
              onUpdate (props) {
                component.updateProps(props)

                popup[0].setProps({
                  getReferenceClientRect: props.clientRect
                })
              },
              onKeyDown (props) {
                if (props.event.key === 'Escape') {
                  popup[0].hide()

                  return true
                }

                return component.ref?.onKeyDown(props)
              },
              onExit () {
                popup[0].destroy()
                component.destroy()
              }
            }
          }
        }
      }),
      Button,
      CustomImage.configure({
        upload: uploadFile
      }),
      TextAlign.configure({
        defaultAlignment: 'left',
        types: ['heading', 'paragraph'],
        alignments: ['left', 'center', 'right']
      }),
      Underline,
      TextStyle,
      Link.configure({
        openOnClick: false
      })
    ]

    this.editor = new Editor({
      extensions,
      autofocus: this.autofocus,
      onUpdate: ({ editor }) => {
        if (this.editor.isActive('customimage') && !this.editor.getAttributes('customimage').uploading) {
          this.imgWidth = this.editor.getAttributes('customimage').width
          this.imageLinkUrl = this.editor.getAttributes('customimage').url
        }
        this.$emit('input', editor.getHTML())
      },
      onSelectionUpdate: ({ editor }) => {
        if (this.editor.isActive('customimage') && !this.editor.getAttributes('customimage').uploading) {
          this.imgWidth = this.editor.getAttributes('customimage').width
          this.imageLinkUrl = this.editor.getAttributes('customimage').url
        }
        if (this.editor.isActive('button') && !this.editor.getAttributes('button').uploading) {
          this.btnLinkUrl = this.editor.getAttributes('button').href
        }
        this.$emit('input', editor.getHTML())
      },
      onFocus: () => {
        this.$emit('focus')
      },
      content: this.value
    })
  },

  beforeUnmount () {
    this.editor.destroy()
  },

  computed: {
  },

  methods: {
    openLink () {
      this.showLinkPop = true
      this.linkUrl = this.editor.getAttributes('link').href

      this.$nextTick(() => {
        this.$refs.url.focus()
      })
    },
    hideLinkPop () {
      setTimeout(() => {
        this.showLinkPop = false
      }, 200)
    },
    onSubmitLink () {
      this.showLinkPop = false

      // empty
      if (this.linkUrl === '') {
        this.editor
          .chain()
          .focus()
          .extendMarkRange('link')
          .unsetLink()
          .run()

        return
      }

      // update link
      this.editor
        .chain()
        .focus()
        .extendMarkRange('link')
        .setLink({ href: this.linkUrl, target: '_blank' })
        .run()

      this.linkUrl = ''
    },
    unsetLink () {
      this.linkUrl = ''
      this.editor
        .chain()
        .focus()
        .extendMarkRange('link')
        .unsetLink()
        .run()
    },
    unsetImageURL () {
      this.imageLinkUrl = null
      this.editor.commands.updateAttributes('customimage', { url: null })
    },
    unsetBtnURL () {
      this.btnLinkUrl = null
      this.editor.commands.updateAttributes('button', { url: null })
    },
    uploadImage (e) {
      const files = e.target.files || e.dataTransfer.files
      if (!files.length) return
      const image = files[0]
      this.editor
        .chain()
        .focus()
        .setImage({ upload: image, uploadFnc: uploadFile })
        .run()
    }
  }
}
</script>

<style lang="scss">
