    display: flex;
  }
  .w-100 {
    width: 100%
  }
  .center {
    text-align: center;
    padding: 4rem
  }

  .ptag {
    color: #555;
    background-color: rgba(#555, 0.1);
    border-radius: 0.3rem;
    padding: 0.1rem 0.3rem;
  }
  .mention {
    color: #1322c4;
    background-color: rgba(#1322c4, 0.1);
    border-radius: 0.3rem;
    padding: 0.1rem 0.3rem;
  }

  div[data-type=button] {
    background-color: var(--btn-bg);
    color: var(--btn-color);
    border-radius: var(--btn-br);
    margin: 1em 0
  }

  .bubble-menu {
    display: flex;
    color: #fafafa;
    background-color: #0D0D0D;
    padding: 0.2rem;
    border-radius: 0.3rem;

    button {
      border: none;
      background: none;
      color: #FFF;
      font-size: 0.85rem;
      font-weight: 500;
      padding: 0 0.2rem;
      opacity: 0.6;

      svg {
        width: 18px;
        height: 18px;
      }

      &:hover,
      &.is-active {
        opacity: 1;
      }
    }

    .link {
      z-index: 1;
      padding: 0.3rem;
      position: absolute;
      background-color: #fafafa;
      border-radius: 0.2rem;
      box-shadow:
        0 0 0 1px rgba(0, 0, 0, 0.1),
        0px 10px 20px rgba(0, 0, 0, 0.1)
      ;

      form {
        display: flex;
        align-items: center;
      }

      input {
        border: 1px solid #bbb;
        min-width: 200px;
        border-radius: 0.2rem;
        outline: 0;
        padding: 3px
      }

      button {
        border: none;
        background: none;
        color: rgb(119, 8, 8);
        font-size: 0.85rem;
        font-weight: 500;
        padding: 0 0.2rem;
        opacity: 0.6;

        &:hover,
        &.is-active {
          opacity: 1;
        }
      }
    }
  }

  .floating-menu {
    display: flex;
    background-color: #0D0D0D10;
    padding: 0.2rem;
    border-radius: 0.3rem;

    button {
      border: none;
      background: none;
      font-size: 0.85rem;
      font-weight: 500;
      padding: 0 0.2rem;
      opacity: 0.6;

      svg {
        width: 18px;
        height: 18px;
      }

      &:hover,
      &.is-active {
        opacity: 1;
      }
    }
  }

  .image-wrp {
    z-index: 1;
    padding: 1rem;
    position: absolute;
    width: 50%;
    max-height: 50%;
    overflow-y: scroll;
    left: 25%;
    background-color: #fafafa;
    border-radius: 0.2rem;
    box-shadow:
      0 0 0 1px rgba(0, 0, 0, 0.1),
      0px 10px 20px rgba(0, 0, 0, 0.1)
    ;

    nav {
      display: flex;
      margin-bottom: 1rem;

      a {
        display: block;
        padding-right: 0.8rem;
        color: #777;
        text-decoration: none;

        &:hover,
        &.is-active {
          color: #000;
        }
      }
