import { Node, mergeAttributes } from '@tiptap/core'

export default Node.create({
  name: 'button',
  group: 'block',
  content: 'inline*',
  allowGapCursor: true,
  atom: true,

  parseHTML () {
    return [
      {
        tag: 'div[data-type="button"]'
      }
    ]
  },
  addOptions () {
    return {
      HTMLAttributes: {}
    }
  },
  addAttributes () {
    return {
      href: {
        default: null
        // parseHTML: element => element.getAttribute('data-href'),
        // renderHTML: attributes => {
        //   if (!attributes.href) {
        //     return {}
        //   }
        //   return {
        //     'data-href': attributes.href
        //   }
        // }
      },
      style: {
        default: null,
        renderHTML: () => {
          return {
            style: 'display:inline-block;padding:10px 20px;text-decoration:none'
          }
        }
      }
    }
  },
  renderHTML ({ HTMLAttributes }) {
    return ['div', mergeAttributes({ 'data-type': 'button' }, this.options.HTMLAttributes, HTMLAttributes), 0]
  },

  addCommands () {
    return {
      addButton: () => ({ commands }) => {
        return commands.setNode(this.name)
      }
    }
  }

  // addKeyboardShortcuts () {
  //   return {
  //     'Mod-Alt-0': () => this.editor.commands.setParagraph()
  //   }
  // }
})
